/**
 * Quick actions communication API
 */
export abstract class AccountsPaymentsCommunication {
  /**
   * Initiates repay event
   *
   * @param arrangementId
   */
  abstract repayEvent(arrangementId: string): void;

  /**
   * Initiates cash in advance event
   *
   * @param arrangementId
   */
  abstract cashAdvanceEvent(arrangementId: string): void;

  /**
   * Initiates pay now event
   *
   * @param arrangementId
   */
  abstract payNowEvent(arrangementId: string): void;
}
